:root {
  --background: white;
  --text-primary: black;
  --text-secondary: royalblue;
  --accent: #7CB9E8;
  --card-color:#00308F;
}

[data-theme='dark'] {
  --background: rgb(23, 4, 129);
  --text-primary: white;
  --text-secondary: grey;
  --accent: #F0F8FF;
  --card-color:#CCCCFF;
}
.App {
  text-align: center;
  color: var(--text-primary);
  background-color: var(--background);
  padding: 20px;
  height: calc(100vh - 40px);
  font-size: large;
  font-weight: bold;
  transition: all .5s;
}
.container{
  position:fixed;
  margin-left: 12px;
}

.container .card{
  position: relative;
  width: 320px;
  height: 320px;
  background: var(--card-color);
  border-radius: 20px;
  overflow: hidden;
}

.container .card:before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--accent);
  clip-path: circle(150px at 80% 20%);
  transition: 0.5s ease-in-out;
}

.container .card:hover:before{
  clip-path: circle(300px at 80% -20%);
}

.container .card .imgBx{
  position: absolute;
  top: 25%;
  transform: translateY(-50%);
  z-index: 10000;
  width: 100%;
  height: 20px;
  transition: 0.5s;
}

.container .card:hover .imgBx{
  top: 0%;
  transform: translateY(0%);
    
}

.container .card .imgBx img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-25deg);
  width: 270px;
}

.container .card .contentBx{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 70px;
  text-align: center;
  transition: 1s;
  z-index: 10;
}

.container .card:hover .contentBx{
  height: 100px;
}

.container .card .contentBx h2{
  position: relative;
  font-weight: 600;
  letter-spacing: 1px;
  color: var(--background);
  margin: 0;
}