body {
    background: #00308F;
  }
  
  .todo-container {
    background: #fff;
    width: 40vw;
    margin: 10em auto;
    border-radius: 15px;
    padding: 20px 10px;
    color:#000;
    border: 3px solid rgb(36, 110, 194);
  }
  
  .task {
    border: 1px solid #00308F;
    border-radius: 5px;
    padding: 0.5em;
    margin: 0.5em;
  }
  
  .task button{
    background: rgb(12, 124, 251);
    border-radius: 5px;
    margin: 0px 5px;
    padding: 3px 5px;
    border: none;
    cursor: pointer;
    color: white;
    float: right;
  }
  
  .header {
    margin: 0.5em;
    font-size: 2em;
    text-align: center;
  }
  
  .create-task input[type=text] {
    width: 95%;
    border-radius: 5px;
    outline: none;
    border: none;
    padding: 0.5em;
    margin: 0.5em;
    background-color: #00308F;
    color: #fff;
  }